// components/reports-view.tsx
"use client"

import * as React from "react"
import { ArrowLeft, Plus, FileText, Clock, CheckCircle2, Calendar } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import Link from "next/link"
import { useProject } from "@/hooks/use-projects"
import { useReports } from "@/hooks/use-reports"
import { Skeleton } from "@/components/ui/skeleton"
import { useRouter } from "next/navigation"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { AlertCircle } from "lucide-react"

type ReportsViewProps = {
  projectId: string
}

const ITEMS_PER_PAGE = 6

export function ReportsView({ projectId }: ReportsViewProps) {
  const router = useRouter()
  const { data: project, isLoading: projectLoading } = useProject(projectId)
  const { data: reports = [], isLoading: reportsLoading } = useReports(projectId)
  const [currentPage, setCurrentPage] = React.useState(1)

  const isLoading = projectLoading || reportsLoading

  React.useEffect(() => {
    if (project && !project.analysisComplete) {
      router.push(`/project/${projectId}`)
    }
  }, [project, projectId, router])

  const totalPages = Math.ceil(reports.length / ITEMS_PER_PAGE)
  const startIndex = (currentPage - 1) * ITEMS_PER_PAGE
  const endIndex = startIndex + ITEMS_PER_PAGE
  const paginatedReports = reports.slice(startIndex, endIndex)

  if (isLoading) {
    return (
      <div className="space-y-6">
        <div className="flex items-center gap-4">
          <Skeleton className="h-10 w-10" />
          <Skeleton className="h-9 w-96" />
        </div>
        <Skeleton className="h-20" />
        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          {Array.from({ length: 6 }).map((_, i) => (
            <Skeleton key={i} className="h-48" />
          ))}
        </div>
      </div>
    )
  }

  if (!project) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center space-y-4">
          <p className="text-muted-foreground">Projet introuvable</p>
          <Button asChild>
            <Link href="/projects">Retour aux projets</Link>
          </Button>
        </div>
      </div>
    )
  }

  if (!project.analysisComplete) {
    return (
      <div className="space-y-6">
        <div className="flex items-center gap-4">
          <Button
            variant="ghost"
            size="icon"
            onClick={() => router.back()}
          >
            <ArrowLeft className="h-5 w-5" />
          </Button>
          <div>
            <h1 className="text-3xl font-semibold tracking-tight text-foreground">
              Rapports Hebdomadaires
            </h1>
            <p className="text-sm text-muted-foreground mt-1">
              {project.name}
            </p>
          </div>
        </div>

        <Alert variant="destructive">
          <AlertCircle className="h-4 w-4" />
          <AlertTitle>Analyse Incomplte</AlertTitle>
          <AlertDescription>
            L'analyse du projet doit tre termine avant de pouvoir gnrer des rapports.
          </AlertDescription>
        </Alert>

        <Button asChild>
          <Link href={`/project/${projectId}`}>Retour au projet</Link>
        </Button>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-4">
          <Button
            variant="ghost"
            size="icon"
            onClick={() => router.back()}
          >
            <ArrowLeft className="h-5 w-5" />
          </Button>
          <div>
            <h1 className="text-3xl font-semibold tracking-tight text-foreground">
              Rapports Hebdomadaires
            </h1>
            <p className="text-sm text-muted-foreground mt-1">
              {project.name}
            </p>
          </div>
        </div>
        <Button asChild>
          <Link href={`/project/${projectId}/reports/create`}>
            <Plus className="mr-2 h-4 w-4" />
            Crer un Rapport
          </Link>
        </Button>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Rsum</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid gap-4 md:grid-cols-3">
            <div>
              <span className="text-sm text-muted-foreground">Rapports crs</span>
              <p className="text-2xl font-bold">{reports.length}</p>
            </div>
            <div>
              <span className="text-sm text-muted-foreground">Rapports publis</span>
              <p className="text-2xl font-bold">
                {reports.filter(r => r.status === "Published").length}
              </p>
            </div>
            <div>
              <span className="text-sm text-muted-foreground">Brouillons</span>
              <p className="text-2xl font-bold">
                {reports.filter(r => r.status === "Draft").length}
              </p>
            </div>
          </div>
        </CardContent>
      </Card>

      {reports.length === 0 ? (
        <div className="flex items-center justify-center min-h-[300px]">
          <div className="text-center">
            <p className="text-muted-foreground">Aucun rapport cr pour ce projet</p>
            <Button className="mt-4" asChild>
              <Link href={`/project/${projectId}/reports/create`}>
                <Plus className="mr-2 h-4 w-4" />
                Crer votre premier rapport
              </Link>
            </Button>
          </div>
        </div>
      ) : (
        <>
          <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
            {paginatedReports.map((report) => (
              <Card key={report.id} className="hover:border-primary/50 transition-colors">
                <CardContent className="p-6 space-y-4">
                  <div className="space-y-3">
                    <div className="flex items-start justify-between">
                      <h3 className="text-lg font-semibold text-foreground">
                        Semaine #{report.weekNumber}
                      </h3>
                      {report.status === "Published" && (
                        <CheckCircle2 className="h-5 w-5 text-green-500 flex-shrink-0 ml-2" />
                      )}
                    </div>
                    
                    <Badge 
                      variant="outline" 
                      className={
                        report.status === "Published"
                          ? "bg-green-500/10 text-green-500 border-green-500/20"
                          : "bg-gray-500/10 text-gray-500 border-gray-500/20"
                      }
                    >
                      {report.status === "Published" ? "Publi" : "Brouillon"}
                    </Badge>
                  </div>

                  <div className="space-y-2 text-sm text-muted-foreground">
                    <div className="flex items-center gap-2">
                      <Calendar className="h-4 w-4" />
                      <span>
                        {new Date(report.startDate).toLocaleDateString('fr-FR', { day: 'numeric', month: 'short' })}
                        {' - '}
                        {new Date(report.endDate).toLocaleDateString('fr-FR', { day: 'numeric', month: 'short', year: 'numeric' })}
                      </span>
                    </div>
                    <div className="flex items-center gap-2">
                      <Clock className="h-4 w-4" />
                      <span>
                        Cr le {new Date(report.createdAt).toLocaleDateString('fr-FR', {
                          day: 'numeric',
                          month: 'short'
                        })}
                      </span>
                    </div>
                  </div>

                  <div className="pt-2 border-t">
                    <div className="text-sm">
                      <span className="text-muted-foreground">Progression: </span>
                      <span className="font-medium">tape {report.currentStep + 1}/5</span>
                    </div>
                  </div>

                  <Button 
                    asChild 
                    className="w-full"
                    variant="outline"
                  >
                    <Link href={`/project/${projectId}/reports/${report.id}`}>
                      <FileText className="mr-2 h-4 w-4" />
                      {report.status === "Draft" ? "Continuer" : "Voir Dtails"}
                    </Link>
                  </Button>
                </CardContent>
              </Card>
            ))}
          </div>

          {totalPages > 1 && (
            <div className="flex items-center justify-center gap-2 pt-4">
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage((p) => Math.max(1, p - 1))}
                disabled={currentPage === 1}
              >
                Prcdent
              </Button>
              <div className="flex items-center gap-1">
                {Array.from({ length: totalPages }, (_, i) => i + 1).map((page) => (
                  <Button
                    key={page}
                    variant={currentPage === page ? "default" : "outline"}
                    size="sm"
                    onClick={() => setCurrentPage(page)}
                    className="w-10"
                  >
                    {page}
                  </Button>
                ))}
              </div>
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage((p) => Math.min(totalPages, p + 1))}
                disabled={currentPage === totalPages}
              >
                Suivant
              </Button>
            </div>
          )}
        </>
      )}
    </div>
  )
}